.menu {
    z-index: 101;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: translateY(-100%);
    transition: .9s;
    font-family: 'IBM Plex Sans' ,monospace;
    font-weight: 500;
    font-size: 16px;
    line-height: 16px;
    color: #292929;
    overflow: hidden;
    background-color: $white;
    
    &:target {
      transform: translateY(0);
      overflow: hidden;
    }

    &__top {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      margin: 30px 25px 25px;
    }

    &__cross {
      background-image: url(../images/cross-vector.svg);
      width: 16px;
      height: 16px;
    }
}

.nav {
  display: flex;
  flex-direction: column;
  margin-left: 30px;

  &__element {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: #333;
    transition-duration: 0.5s;

    &:hover {
      font-weight: 700;
      font-size: 14px;
      line-height: 21px;
      color: #131313;
      filter: drop-shadow(0 0 0.3px #333);
      transform: scale(1.01);
    }
  }

  &__wrap {
    display: flex;
    flex-direction: column;
    gap: 25px;
  }

  &__specs {
    margin-top: 50px;
    display: flex;
    flex-direction: row;
    font-family: Helvetica, sans-serif;
    font-size: 14px;
    line-height: 18px;

    @include tablet {
      margin: 0;
    }
  }

  &__en {
    font-size: 14px;
    line-height: 18px;
    transition: .9s;
    font-family: Helvetica, sans-serif;
    margin-left: 20px;
    margin-right: 3px;
  }

  &__ua {
    font-size: 14px;
    line-height: 18px;
    transition: .9s;
    font-family: Helvetica, sans-serif;
    margin-left: 3px;
  }
}



.darkgray {
  color: #131313;
  pointer-events: none;
}

.gray {
  color: #BDBDBD;
  transition-duration: 0.5s;

  &:hover {
    filter: drop-shadow(0 0 0.3px #dbdbdb);
    transform: scale(1.05);
  }
}

.lang-phone {
  display: block;

  @include tablet {
    display: none;
  }
}

.button-phone {
  max-width: 100vh;
  display: flex;
  height: 48px;
  background-color: #333;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-family: Helvetica, sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 14.65px;
  margin: 30px;
  padding: 0 120px;
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  transition-duration: 0.5s;
  &:hover {
    background-color: #131313;
  }

  @include tablet {
    display: none;
  }
}
